

/* Nav component */
* {
  margin: 0;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

body {
  /* background-color: #016A53; */
  background-color: #252525;

  background-image: url('./bilder/bg/bg-top-1.png');

  background-repeat: no-repeat;
  background-size: 70rem;

  font-family: "Spartan", Courier, monospace;
}

.wrapper {
  /* 1300px = avoid the horizontal scroll bar */
  width: 1300px;
}

/* html tag's*/

p {
  font-family: "Inconsolata", monospace;
  letter-spacing: 0.1em;
  line-height: 1.3em;
}

h2 {
  color: #f57f12;
  margin-bottom: 20px;
}

a {
  color: #f4e7d2;
  text-decoration: none;
  font-size: 27px;
  -webkit-text-stroke: 1px #252525;

  font-weight: 900;
}

::selection {
  background-color: purple;
  color: yellow;
  -webkit-text-stroke: 1px #016a53;
}
/* / html tag */

/* img */
.splash {
  background: url("./bilder/mainLeftSplash.png");
  transform: rotate(180deg);
  object-fit: cover;

  background-repeat: no-repeat;
  width: 250px;
  height: 250px;
  position: relative;
  z-index: 1;
}
/* Nav bar */
.flex--nav-box {
  /* MoveBoksen til Nav_Sectionen*/
  position: relative;
 left: 100px;
  display: flex;
  width: 100%; /* Litt støre en link boksen */

  position: fixed;
  z-index: 2;

  font-family: "Luckiest Guy", cursive;
 
}

/* Animation bevgelse på nav logoen */
.nav-splash-img {
  position: relative;
  animation: splash 2s ease 1s infinite alternate;
}

@keyframes splash {
  from {
    transform: scale(100%);
  }
  to {
    transform: scale(90%);
  }
}

/* To make the nav bar stopp some px above */
.section-position {
  position: relative;
  top: 100px;
  width: 98.5vw;
}
/* To make the nav bar stopp some px above in the contactMe section */
.section-position-higher {
  position: relative;
  top: 200px;
}

/* / Animation bevgelse på nav logoen */

.flex--nav-link {
  position: absolute;
  /* border: 2px solid blue; */
  width: 800px; /* Bredden link boksen */
  
  top: 0;
  height: 70px;
 
  
  display: flex;
  justify-content: space-around;
  align-items: flex-end;
 

 

}


.nav-icon-home {
  color: #f57f12;
}

/* using this class some places where color #F57F12 are added */
.letter-space {
  letter-spacing: 0.6rem;
}

.nav-icon-home:hover,
.nav-icon-about-me:hover,
.nav-icon-skills:hover,
.nav-icon-prosjekter:hover,
.nav-icon-contact-me:hover {
  transform: scale(105%);
  font-weight: 900;
}

