/*  icon */
.iconHTML,
.iconCSS,
.iconFIGMA,
.iconJS,
.iconREACT,
.iconTypeScript {
  width: 40px;
  animation-name: icons;
  animation-duration: 5s;
  animation-timing-function: ease-out;
  animation-delay: 2s;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;

  cursor: pointer;
}

@keyframes icons {
  from {
  }
  to {
    margin-left: 100px;
  }
}

/* When hover at skill icon's at <home /> */
.iconHTML:hover,
.iconCSS:hover,
.iconFIGMA:hover,
.iconJS:hover,
.iconREACT:hover,
.iconTypeScript:hover {
  transform: scale(120%);
  box-shadow: 0 0 5px #f4e7d2;
}

/* when icon get's clicked */
.toggleDiv {
  position: relative;
  top: 20px;
  width: 420px;

  background-color: #666;
  border: 2px dotted #f57f12;
  border-radius: 20px;

  color: #f4e7d2;
  padding: 20px;

  animation: fade-down 2000ms forwards;
  transform: translateY(-100px);
  opacity: 0;
  z-index: 2;
}

/* To make the divBox drop down */
@keyframes fade-down {
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
