/* Where the Nav Router Buttons are on the page */
.Nav-header {
  position: absolute;
  
 display: flex;
 flex-direction: column;
 
  
  gap: 10px;
  
  top: 315px;
  left: -40px;
}
/* Router dom, grid setup */
.routerCounter {
  color: #252525 !important;
  
  background-image: none !important;
}
.routerDrumkit {
  color: #252525 !important;
  background-image: none !important;
}
.routerToDoList {
  color: #252525 !important;
  background-image: none !important;
}
.routerNettStore {
  
  color: #252525 !important;
  background-image: none !important;
}
.routerNew {
  color: #252525 !important;
  background-image: none !important;
}

/* Router Link Knappene */
.Nav-header > a {
  background-image: url("./bilder/background.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-color: #f57f12;
  border-radius: 15px;
  padding: 10px;
  
  z-index: 1;
  border: 2px solid #f57f12;
  font-size: 16px ;
  color: white;
  
}
.Nav-header > a:active {
  box-shadow: 0 10px 30px white;
}
.Nav-header > a:hover {
  transform: scale(105%);
}
